import Vue from 'vue';
import Vuex from 'vuex';


Vue.use(Vuex);

export const store = new Vuex.Store({
	state:{
		website:{
			title:'Distribuidora Biarritz',
			searcherActive:false,
			colors:{
				primary: '#e74c3c',
				secondary: '#424242',
				accent: '#FF8A80',
				error: '#FF5252',
				info: '#2196F3',
				success: '#4CAF50',
				warning: '#FFC107'
			},
			
		},
		productsSearcher:'',
		products: [
          { id:1,avatar:'http://regionalsur.com.uy/wp-content/uploads/2016/01/Maiz-800g-01.jpg', title: 'Choclo', category: 'enlatados' , price:'$14'},
          { id:2,avatar:'https://d3lfzbr90tctqz.cloudfront.net/epi/resource/r/auriculares-sony-mdr-ex15apbzuc-negro/3704001fb43987061fc2e4393fbd8d483431c2d2910fb7e29de990f00e49e77f_500', title: 'Auriculares Sony', category: 'tecnologia', price:'$14' },
          { id:3,avatar:'https://d3lfzbr90tctqz.cloudfront.net/epi/resource/r/auriculares-sony-mdr-ex15apbzuc-negro/3704001fb43987061fc2e4393fbd8d483431c2d2910fb7e29de990f00e49e77f_500', title: 'Auriculares Sony', category: 'tecnologia', price:'$14' },
          { id:4,avatar:'https://www.supervivencia.org/imagenesarticulos/agua-lavandina.jpg', title: 'Lavandina', category: 'limpieza' , price:'$14'},
          { id:5,avatar:'https://www.supervivencia.org/imagenesarticulos/agua-lavandina.jpg', title: 'Lavandina', category: 'limpieza' , price:'$14'},
          { id:6,avatar:'https://www.supervivencia.org/imagenesarticulos/agua-lavandina.jpg', title: 'Lavandina', category: 'limpieza' , price:'$14'},
          { id:7,avatar:'https://www.supervivencia.org/imagenesarticulos/agua-lavandina.jpg', title: 'Lavandina', category: 'limpieza' , price:'$14'},
          { id:8,avatar:'https://www.supervivencia.org/imagenesarticulos/agua-lavandina.jpg', title: 'Lavandina', category: 'limpieza' , price:'$14'},
          { id:9,avatar:'https://www.supervivencia.org/imagenesarticulos/agua-lavandina.jpg', title: 'Lavandina', category: 'limpieza' , price:'$14'}
        ],
	},
	getters:{
		getWebsiteInfo(state)
		{
			return state.website;
		},
		getProducts(state)
		{
			return state.products.filter((product) => product.title.toLowerCase().includes(state.productsSearcher.toLowerCase()));
		}
	},
	mutations:{
		changeSearcherState(state,value)
		{
			state.productsSearcher = value;
		},
		SearcherActiveState(state, value)
		{
			state.website.searcherActive = value;
		}
	},
	actions:{

	}
});


