<template>
  <div id="app">
    <v-app id="inspire">
      <router-view/>
    </v-app>
  </div>
</template>

<script>

export default {
  mounted()
  {
    this.setTitle();
  },
  methods:{
    setTitle()
    {
      document.title=this.website.title;
    }
  },
  computed:{
    website()
    {
      return this.$store.getters.getWebsiteInfo;
    }
  },
  watch:{
    '$route'(to,from)
    {
      this.setTitle();
    }
  }
}
</script>

<style>
</style>



